export const DayPickerStory = addSourceDecorator(function DayPickerStory() {
  const [selectedDate, setSelectedDate] = useState<string | null>(null)
  const beforeBlock = useOptionalKnob({
    name: 'beforeBlock',
    knob: date,
    initialValue: today,
  })
  const afterBlock = useOptionalKnob({
    name: 'afterBlock',
    knob: date,
    initialValue: initialAfterBlock,
  })
  const disabledDays = useOptionalKnob({
    name: 'disabledDays',
    knob: array,
    initialValue: initialDisabledDates,
    extraKnobParams: ['\n'],
  })

  button('날짜 선택 리셋', () => {
    setSelectedDate(null)
  })

  return (
    <DayPicker
      day={selectedDate}
      onDateChange={(date) => {
        action('날짜 선택')(date)
        setSelectedDate(date.toISOString())
      }}
      beforeBlock={beforeBlock ? new Date(beforeBlock) : undefined}
      afterBlock={afterBlock ? new Date(afterBlock) : undefined}
      disabledDays={
        disabledDays
          ? disabledDays.filter(checkValidISODateFormat).filter(checkValidDate)
          : undefined
      }
      height={text('높이', '300px')}
      publicHolidays={array('공휴일', initialHolidays, '\n')
        .filter(checkValidDate)
        .map((date) => new Date(date))}
      numberOfMonths={number('표시할 개월 수', 3)}
    />
  )
}, {__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__});

DayPickerStory.story = {
  name: '단일 날짜 선택 컴포넌트',
}

export const RangePickerStory = addSourceDecorator(function RangePickerStory() {
  const [{ startDate, endDate }, setDateRange] = useState<{
    startDate: string | null
    endDate: string | null
  }>({
    startDate: null,
    endDate: null,
  })

  const beforeBlock = useOptionalKnob({
    name: 'beforeBlock',
    knob: date,
    initialValue: today,
  })
  const afterBlock = useOptionalKnob({
    name: 'afterBlock',
    knob: date,
    initialValue: initialAfterBlock,
  })
  const disabledDays = useOptionalKnob({
    name: 'disabledDays',
    knob: array,
    initialValue: initialDisabledDates,
    extraKnobParams: ['\n'],
  })

  button('날짜 선택 리셋', () => {
    setDateRange({
      startDate: null,
      endDate: null,
    })
  })

  return (
    <RangePicker
      startDate={startDate}
      endDate={endDate}
      startDateLabel={text('startDateLabel', '출국일')}
      endDateLabel={text('endDateLabel', '귀국일')}
      sameDateLabel={text('sameDateLabel', '당일 왕복')}
      onDatesChange={({ startDate, endDate, nights }) => {
        action('날짜 선택')({ startDate, endDate, nights })
        setDateRange({ startDate, endDate })
      }}
      beforeBlock={beforeBlock ? new Date(beforeBlock) : undefined}
      afterBlock={afterBlock ? new Date(afterBlock) : undefined}
      disabledDays={
        disabledDays
          ? disabledDays.filter(checkValidISODateFormat).filter(checkValidDate)
          : undefined
      }
      publicHolidays={array('공휴일', initialHolidays, '\n')
        .filter(checkValidDate)
        .map((date) => new Date(date))}
      numberOfMonths={number('표시할 개월 수', 3)}
      height={text('높이', '300px')}
      enableSameDay={boolean('enableSameDay 활성화', false)}
    />
  )
}, {__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__});
