function ScrollToAnchorComponent({ useAlias }: { useAlias: boolean }) {
  useEffect(() => {
    window.history.pushState(null, '', '#app')
  }, [])

  useScrollToAnchor({
    ...(useAlias
      ? {
          alias: {
            app: 'alias',
          },
        }
      : {}),
  })

  return (
    <div>
      <div style={{ height: '200vh' }}></div>
      <div id="app">
        <h1>App</h1>
      </div>
      <div style={{ height: '200vh' }}></div>
      <div id="alias">
        <h1>Alias</h1>
      </div>
    </div>
  )
}

